import { defineElement, html } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { skeletonStyles } from './skeletonStyle';
import { SkeletonCircleProperties } from './skeletonCircleProperties';
import { Skeleton } from './skeleton';

@defineElement({ tagName: 'skeleton-circle', styles: skeletonStyles, version })
export class SkeletonCircle extends Skeleton implements SkeletonCircleProperties {
    // Public members

    protected renderInner(baseClassName, rootClasses) {
        return html`
            <label class="${rootClasses}">
                <div
                    class="${baseClassName}-background-color"
                    style="height: ${this.height}; width: ${this.width}; border-radius: 50%;"
                ></div>
            </label>
        `;
    }
}
