import { defineElement, html } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { skeletonStyles } from './skeletonStyle';
import { SkeletonImageProperties } from './skeletonImageProperties';
import print from '@umetrics/sartorius-ui-icon/dist/icons/print';
import { Skeleton } from './skeleton';

@defineElement({
    tagName: 'skeleton-image',
    styles: skeletonStyles,
    version,
    //sub: [Icon]
})
export class SkeletonImage extends Skeleton implements SkeletonImageProperties {
    private static Icon;

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];

        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });

        SkeletonImage.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    protected renderInner(baseClassName, rootClasses) {
        return html`
            <label class="${rootClasses}">
                <div class="${baseClassName}-background-color ${baseClassName}-image-background" 
                style="width: ${this.width}; height: ${this.height};">
                    <${SkeletonImage.Icon.tag} icon="${print}" 
                    style="font-size: 3rem;"></${SkeletonImage.Icon.tag}>
                </div>
            </label>
        `;
    }
}
